// 主题色全局变量
:root {
  --themeColor: rgba(0,0,0,1); 
  --themeBgColor: rgba(242,242,242,1);
}
$themeColor: var(--themeColor); // 主要用于字体、图标
$themeBgColor: var(--themeBgColor); // 主要用于按钮、色块的背景色

// 白色
$whiteColor: white;
$whiteBg: white;

// 黑色
$blackBg: black;
$blackColor: black;
// border
@mixin border($width: 1px, $color: $blackColor, $radius: 4px, $style: solid) {
  border: {
    width: $width;
    color: $color;
    radius: $radius;
    style: $style;
  }
}
// font
@mixin font($width: 14px, $color: $blackColor, $weight: 400) {
  color: $color;
  font: {
    size: $width;
    weight: $weight;
  }
}
// 文字溢出显示...
@mixin textEllipsis{
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
// flex居中
@mixin flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
// 垂直水平居中
@mixin positionCenter{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.hello{
  width: 100vw;
  height: 100vh; 
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: $whiteBg;
}